 <style scoped lang="scss">
:root,
.md-button.md-theme-default.md-primary,
.md-field.md-theme-default.md-focused label {
  --md-theme-default-primary: #d24744;
  --md-theme-default-primary-on-background: #d24744;
}
.md-progress-bar {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
}
.md-layout {
  max-width: 400px;
}
.center-container {
  background-color: #ececec;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  width: 100vw;
  height: 100vh;
  img {
    margin-bottom: 2em;
    margin-top: -5em;
  }
  .md-title {
    color: lightgrey;
  }
}
</style>
 
 <template>
     <div class="center-container">     
        <img class="logo" src="@/assets/logo/logoAsys.png" alt="Logo Asys">
        <form novalidate class="md-layout" @submit.prevent="validateUser">
            <md-card class="md-layout-item">
                <md-card-header>
                    <div class="md-title">Reset Password</div>
                </md-card-header>

                <md-card-content>
                    <div class="md-layout md-gutter">
                        <div class="md-layout-item md-small-size-100">
                        <md-field :class="getValidationClass('password')">
                            <label for="first-name">New Password</label>
                            <md-input type="password" name="password" id="password" autocomplete="given-name" v-model="password" :disabled="ifLoading" />
                            <span class="md-error" v-if="!$v.password.required">The password name is required</span>
                            <span class="md-error" v-else-if="!$v.password.minlength">Invalid password</span>
                        </md-field>
                        </div>

                        <div class="md-layout-item md-small-size-100">
                        <md-field :class="getValidationClass('passwordRepeat')">
                            <label for="passwordRepeat">Repeat Password</label>
                            <md-input type="password" name="passwordRepeat" id="passwordRepeat" autocomplete="family-name" v-model="passwordRepeat" :disabled="ifLoading" />
                            <span class="md-error" v-if="!$v.passwordRepeat.required">The repeated password is required</span>
                            <span class="md-error" v-else-if="!$v.passwordRepeat.minlength">Invalid repeated password</span>
                        </md-field>
                        </div>
                    </div>
                </md-card-content>

                <md-progress-bar md-mode="indeterminate" v-if="ifLoading" />

                <md-card-actions>
                    <md-button type="submit" class="md-primary" :disabled="ifLoading">Reset Password</md-button>
                </md-card-actions>
            </md-card>
        </form>
    </div>

 </template>
 
 <script>
// https://github.com/monterail/vuelidate
import { validationMixin } from "vuelidate";
import { required, minLength } from "vuelidate/lib/validators";

export default {
  name: "Restart",
  mixins: [validationMixin],
  data() {
    return {
      email: this.$route.query.mail,
      token: this.$route.query.token,
      password: null,
      passwordRepeat: null
    };
  },
  validations: {
    password: {
      required,
      minLength: minLength(4)
    },
    passwordRepeat: {
      required,
      minLength: minLength(4)
    }
  },
  computed: {
    ifLoading: function() {
      return this.$store.state.auth.status == "loading";
    }
  },
  methods: {
    restartPassword: function() {
      const { email, token, password } = this;
      this.$store
        .dispatch("auth/AUTH_RESTART", { email, password, token })
        .then(() => {
          this.clearForm();
          this.$router.push("/intranet/dashboard");
        });
    },
    getValidationClass(fieldName) {
      const field = this.$v[fieldName];
      if (field) {
        return {
          "md-invalid": field.$invalid && field.$dirty
        };
      }
    },
    clearForm() {
      this.$v.$reset();
      this.password = null;
      this.passwordRepeat = null;
    },
    validateUser() {
      this.$v.$touch();
      if (!this.$v.$invalid) {
        this.restartPassword();
      }
    }
  }
};
</script>
 